#home {
    .container-fluid {
        padding: 0px;
        margin: 0px;

        .navbar {
            position: fixed;
            width: 100%;
            padding: 1%;
            padding-left: 13%;
            z-index: 99;

            .navbar-brand {
                color: #576c8c;
            }

            .navbar-nav {
                .nav-item {
                    margin-left: 10px;

                    a {
                        color: white;
                    }
                }

                .nav-item a:hover {
                    text-decoration: underline;
                    color: #576c8c;
                }
            }

            @media screen and (max-width: 991px) {
                display: none;
            }
        }

        .sidenav {
            height: 100%;
            width: 0;
            position: fixed;
            z-index: 1000;
            top: 0;
            left: 0;
            background-color: #111;
            overflow-x: hidden;
            transition: 0.5s;
            padding-top: 60px;
            display: none;

            a {
                padding: 8px 8px 8px 32px;
                text-decoration: none;
                font-size: 20px;
                color: #576c8c;
                display: block;
                transition: 0.3s;
                text-align: left;

                &:hover {
                    color: #f1f1f1;
                }
            }

            .closebtn {
                position: absolute;
                top: 0;
                right: 20px;
                font-size: 36px;
                margin-left: 50px;
            }

            @media screen and (max-width: 991px) {
                display: block !important;
            }
        }

        .open {
            @media screen and (max-width: 991px) {
                display: block !important;
                padding: 7px;
                padding-left: 31px;
                padding-left: 7px;
                padding-bottom: 0px;
                padding-top: 0px;
                left: 10px;
                top: 10px;
                position: fixed;
                z-index: 100;
                background-color: #a9a8a8bd;
                line-height: 1.5;
                font-size: 24px !important;
            }
        }

        @media screen and (max-height: 450px) {
            .sidenav {
                padding-top: 15px;
            }

            .sidenav a {
                font-size: 18px;
            }
        }

        .overlay {
            background-color: rgba(62, 63, 63, 0.247);
            width: 100%;
            height: 100%;

            .header-body {
                height: 500px;
                width: 100%;
                background-image: url(../../dist/image/header-image.jpg);
                // opacity: 0.5;
                padding-top: 240px;


                .form {
                    .text-header {
                        color: #fff;
                    }

                    input {
                        margin-left: 43px;
                        padding: 14px 7px;
                        font-size: 17px;
                        outline: none;

                        @media screen and (min-width: 576px) {
                            margin-top: 15px;
                        }
                    }

                    .search-btn {
                        margin-top: 16px;
                        margin-left: 32px;
                        background-color: #0c2d63;
                        color: white;
                        height: 54px;

                        &:hover {
                            background-color: #fff;
                            color: #0c2d63;
                        }
                    }
                }
            }
        }
    }
}